
.story_detail {
    .story_hd {
        width: 100%;
        height: 0.9rem;
        padding: 0.25rem 5%;;
        box-sizing: border-box;
        background: #fff;
        border-bottom: 1px solid #eee;
        position: fixed;
        top: 0;
        i {
            font-size: 0.4rem;
        }
    }
    .story_bd {
        width: 90%;
        margin: 0.9rem auto 0;
        padding-top: 0.3rem;
        .story_msg {
            display: flex;
            img {
                width: 0.8rem;
                height: 0.8rem;
                border-radius: 50%;
                border: 1px solid #000;
            }
            .comment_user {
                line-height: 0.4rem;
                padding-left: 0.3rem;
                span {
                    font-size: 0.26rem;
                }
            }
        }
    }
    .story_content {
        padding: 0.3rem 0;
        img {
            max-width: 100%;
            margin: 0.2rem 0;
        }
        h2 {
            font-size: 0.32rem;
            font-weight: bold;
            margin: 0.3rem 0;
        }
        p {
            font-size: 0.3rem;
            line-height: 0.5rem;
            margin-bottom: 0.5rem;
        }
        span {
            font-size: 0.28rem;
            color: #008489;
        }
    }
    .story_menu {
        width: 100%;
        box-sizing: border-box;
        display: flex;
        justify-content: space-around;
        div {
            width: 2.5rem;
            height: 2.5rem;
            text-align: center;
            border: 1px solid #ddd;
            background: #F9F9F9;
            border-radius: 50%;
            i {
                font-size: 0.5rem;
                line-height: 2.5rem;
                color: #999;
            }   
            .icon-dianzan {
                font-size: 0.7rem;
            }
        }
    }
    .story_ft {
        width: 100%;
        display: flex;
        justify-content: space-around;
        h2 {
            font-size: 0.3rem;
            line-height: 1rem;
            color: #666;
        }
    }
}